import React, { useState, useEffect} from 'react';
import './Components.css';
import profilePic from '../images/jackCrop.jpg'


function qualifications(){
	return(
			<ul>
						<li>BCs with Honours in Ethical Hacking at Abertay University</li>
						<li>SSCP: Systems security Certified practitioner (include SSCP badge)</li>
						<li>The Complete Web Developer in 2020: Zero to Mastery online course (include certificate)</li>
						<li>In progress: Certified Information Systems Security Professional (CISSP)</li>
			</ul>
		);
};
function secExp(){
	return(
			<ul>
						<li>I have studied ethical hacking at university for 4 years, giving me a solid foundation on the technical aspects of Information Security.</li>
						<li>I still do penetration testing as a hobby by completing online penetration testing labs from "hack the box".</li>
						<li>Since graduation, I have worked as a CyberSecurity Analyst at a financial technology company. I have now been at this company for over 1.5 years. My job has varied a lot since I have worked there and I have touched upon a range of different areas. Some of the main responsibilities within my role were:
							<ul>
								<li>Manage the information security risk framework through the maintenance and Development of security controls.</li>
								<li>Ensure compliance with existing policies and controls.</li>
								<li>Manage and perform testing on client platforms.</li>
								<li>Provide client assurance on the effectiveness of information security controls.</li>
								<li>Provide support to our Security Operations Centre.</li>
							</ul>
						</li>
						<li>I have passed the SSCP exam and have gained/maintained certification. This is a globally recognized advanced security administration and operations certification which demonstrates I have the advanced technical skills and knowledge to implement, monitor and administer IT infrastructure using security best practices, policies and procedures established by the cybersecurity experts at (ISC)². Continued certification is based on earning Continuing Professional Education (CPE) points through security related work/training. A minimum amount of points is required each year.</li>
						<li>Throughout my time at university, as well as my time as an analyst I have completed several security related projects. Please look at the project page for more details.</li>
			</ul>
		);
};
function devExp(){
	return(
			<ul>
						<li>During my time at university I sat a module on web application Development, and also had several modules centred around testing web apps.</li>
						<li>After graduating university and securing my first job, i decided I wanted to continue pursuing web application development as a hobby. To do this I decided to sit the online course "The Complete Web Developer in 2020: Zero to Mastery" by Andrei Neagoi. This course covered both front end, and back end development. It helped me to master the fundamental concepts and focussed on teaching modern technologies being used in 2021.</li>
						<li>Please look at the projects page to see web applications and other developer related projects I have worked on.</li>
			</ul>
		);
};


const ExperienceWindow = () => {
	

	

	const [textContent, setTextContent] = useState(qualifications())

	// The following block of code (40 lines) is for changing the button css properties when selected
	const [qualificationButton, setQualificationButton] = useState("buttonTest  w-third h-100")
	const [developerButton, setDeveloperButton] = useState("buttonTest  w-third h-100")
	const [securityButton, setSecurityButton] = useState("buttonTest  w-third h-100")
	const [buttonSelected, SetButtonSelected] = useState("qualifications")
	useEffect(() => {
	   	if(buttonSelected === "security"){
	      	setSecurityButton("buttonTestSelected  w-third h-100 b--dark-red");
	      	setDeveloperButton("buttonTest  w-third h-100");
			setQualificationButton("buttonTest  w-third h-100");
	    }
	    else if (buttonSelected === "developer"){
	    	setDeveloperButton("buttonTestSelected  w-third h-100 b--dark-red");
	    	setSecurityButton("buttonTest  w-third h-100");
	    	setQualificationButton("buttonTest  w-third h-100");
	    }
	    else{
	    	setQualificationButton("buttonTestSelected  w-third h-100");
	    	setSecurityButton("buttonTest  w-third h-100");
	    	setDeveloperButton("buttonTest  w-third h-100");
	    };
	// eslint-disable-next-line
	}, [textContent]);

	function secbutt(){
		SetButtonSelected("security")
		setTextContent(secExp())
	}
	function devbutt(){
		SetButtonSelected("developer")
		setTextContent(devExp())
	}
	function quabutt(){
		SetButtonSelected("qualifications")
		setTextContent(qualifications())
	}

	return(
		<div  className = "windowDesignExperience flex">
			<div className = "outline w-40"><img className="h-100 w-100 v-top" alt="Logo" src={profilePic} /></div>
			<div className = "outline w-60 flex-column">
				<div className = "outline w-100 flex justify-center expColumn1">
					<button className={qualificationButton} onClick={() => quabutt()}>Qualifications</button>
					<button className = {securityButton} onClick={() => secbutt()}>Security Experience</button>
					<button className = {developerButton} onClick={() => devbutt()}>Developer Experience</button>
				</div>
				<div className = "outline w-100 flex justify-center expColumn2">
						{textContent}
				</div>
			</div>
		</div>
	);
};

export default ExperienceWindow;