import React, { useState} from 'react';
import './App.css';
import {Hello, Experience, Projects} from './home/Home.jsx';
import Toolbar from './components/Toolbar.js';
import SideToolbar from './components/ContactBar.js'
import Backdrop from './components/Backdrop.js';
import {Switch, Route, useLocation} from 'react-router-dom';
import {AnimatePresence } from 'framer-motion';
import BackGround1 from './Background';


function App() {
  
const [sideBar, sideBarSet] = useState(false)
const myLocation = useLocation();



  return (

    <main>  
    <BackGround1 />
   
      <div className="" >
         <AnimatePresence exitBeforeEnter>
    <Switch location={myLocation} key={myLocation.pathname}>
            <Route exact path="/" >
              <Hello/>
            </Route>
            <Route exact path="/experience" >
              <Experience />
            </Route>
            <Route exact path="/projects" >
              <Projects />
            </Route>
              </Switch>
      </AnimatePresence>
        <div style = {{marginBottom: "10%"}}/> {/*this is for the toolbar margin*/}

        <Toolbar open = {()=>sideBarSet(true)} />

          {sideBar ? (
            <div>
              <Backdrop close = {()=>sideBarSet(false)} />(" ")
            </div>
          ) : null}
            <SideToolbar close = {()=>sideBarSet(false)} SideToolbarOpen= {sideBar} />
      </div>

    </main>
  );
  

  
}

export default App;
