import {React, useState, useEffect} from 'react';
import './Components.css';
import ProjectSelection from './ProjectSelection.js';

const ProjectToolbar = ({close, projectToolbarOpen, toggleBar, arrowPos, ProjectsDetail, setSelectedID, selectedID}) => {

	const [projectType, setProjectType] = useState("security")
	const [securityButton, setSecurityButton] = useState("buttonTest outline w-third h-100")
	const [developerButton, setDeveloperButton] = useState("buttonTest outline w-third h-100")
	const [otherButton, setOtherButton] = useState("buttonTest outline w-third h-100")
	
	let Classes = "project-nav-bar "

	useEffect(() => {
	    // changing the toolbar button to selected
	   	if(projectType === "security"){
	      	setSecurityButton("buttonTestSelected outline w-third h-100");
	      	setDeveloperButton("buttonTest outline w-third h-100");
			setOtherButton("buttonTest outline w-third h-100");
	    }
	    else if (projectType === "developer"){
	    	setDeveloperButton("buttonTestSelected outline w-third h-100");
	    	setSecurityButton("buttonTest outline w-third h-100");
	    	setOtherButton("buttonTest outline w-third h-100");
	    }
	    else{
	    	setOtherButton("buttonTestSelected outline w-third h-100");
	    	setSecurityButton("buttonTest outline w-third h-100");
	    	setDeveloperButton("buttonTest outline w-third h-100");
	    }
	// eslint-disable-next-line
	}, [projectType]);

	if(projectToolbarOpen){
	 	Classes = "project-nav-bar open "
	}
	return(
		<nav className = {Classes}>
			<div className="toggleButton" onClick={toggleBar}>
				{/*<div className = "w-10" onClick={close}><i className="material-icons bigX">close</i></div>*/}
				<i className="material-icons toggleArrows">{arrowPos}</i>
				<i className="material-icons toggleArrows">{arrowPos}</i>
				<i className="material-icons toggleArrows">{arrowPos}</i>
				<i className="material-icons toggleArrows">{arrowPos}</i>
				<i className="material-icons toggleArrows">{arrowPos}</i>
				<i className="material-icons toggleArrows">{arrowPos}</i>
			</div>
			<div className = "project-nav-bar-inner w-100 flex flex-wrap tc">
				<div className = "outline w-100 flex justify-center proColumn1">
				
					<button className = {securityButton} onClick={() => setProjectType("security")}>Security Projects</button>				
					<button className={developerButton} onClick={() => setProjectType("developer")}>Developer Projects</button>
					<button className = {otherButton} onClick={() => setProjectType("other")}>Other Projects</button>
				</div>
				<div className = "proColumn2"> </div>
				<div className = "w-100 proColumn3 ">
					{(() => {
				        if (projectType === "security") {
				          return (
				            <div>
				            	<ProjectSelection id={ProjectsDetail[8].id} title={ProjectsDetail[8].title} description={ProjectsDetail[8].description} image={ProjectsDetail[8].image} file={ProjectsDetail[8].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
				            	<ProjectSelection id={ProjectsDetail[14].id} title={ProjectsDetail[14].title} description={ProjectsDetail[14].description} image={ProjectsDetail[14].image} file={ProjectsDetail[14].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
				            	<ProjectSelection id={ProjectsDetail[4].id} title={ProjectsDetail[4].title} description={ProjectsDetail[4].description} image={ProjectsDetail[4].image} file={ProjectsDetail[4].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
				            	<ProjectSelection id={ProjectsDetail[3].id} title={ProjectsDetail[3].title} description={ProjectsDetail[3].description} image={ProjectsDetail[3].image} file={ProjectsDetail[3].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
				            	<ProjectSelection id={ProjectsDetail[7].id} title={ProjectsDetail[7].title} description={ProjectsDetail[7].description} image={ProjectsDetail[7].image} file={ProjectsDetail[7].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
				            </div>
				          )
				        } else if (projectType === "developer") {
				          return (
				            <div>
				            	<ProjectSelection id={ProjectsDetail[5].id} title={ProjectsDetail[5].title} description={ProjectsDetail[5].description} image={ProjectsDetail[5].image} file={ProjectsDetail[5].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
								<ProjectSelection id={ProjectsDetail[0].id} title={ProjectsDetail[0].title} description={ProjectsDetail[0].description} image={ProjectsDetail[0].image} file={ProjectsDetail[0].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
								<ProjectSelection id={ProjectsDetail[12].id} title={ProjectsDetail[12].title} description={ProjectsDetail[12].description} image={ProjectsDetail[12].image} file={ProjectsDetail[12].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
				            	<ProjectSelection id={ProjectsDetail[9].id} title={ProjectsDetail[9].title} description={ProjectsDetail[9].description} image={ProjectsDetail[9].image} file={ProjectsDetail[9].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
				            </div>
				          )
				        } else {
				          return (
				            <div>
								<ProjectSelection id={ProjectsDetail[13].id} title={ProjectsDetail[13].title} description={ProjectsDetail[13].description} image={ProjectsDetail[13].image} file={ProjectsDetail[13].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
				            	<ProjectSelection id={ProjectsDetail[1].id} title={ProjectsDetail[1].title} description={ProjectsDetail[1].description} image={ProjectsDetail[1].image} file={ProjectsDetail[1].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
								<ProjectSelection id={ProjectsDetail[2].id} title={ProjectsDetail[2].title} description={ProjectsDetail[2].description} image={ProjectsDetail[2].image} file={ProjectsDetail[2].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
								<ProjectSelection id={ProjectsDetail[6].id} title={ProjectsDetail[6].title} description={ProjectsDetail[6].description} image={ProjectsDetail[6].image} file={ProjectsDetail[6].file} setSelectedID={setSelectedID} selectedID={selectedID}/><div className = "pb4"> </div>
							</div>
				          )
				        }
				      })()}
						
						
				</div>
			</div>
		</nav>
	);
};

export default ProjectToolbar;