import React from 'react';
import './Components.css';
import {NavLink, Switch} from 'react-router-dom'; 

const Toolbar = ({open}) => (
<Switch>
		<nav className="nav-bar">
			
			<div className="nav-items"> 
				<ul>
					<li><div className="nav-icon"><NavLink to="/" >Home</NavLink></div></li>
					<li><div className="nav-icon"><NavLink to="/experience">Experience</NavLink></div></li>
					<li><div className="nav-icon"><NavLink to="/projects">Projects</NavLink></div></li>
					<li><div className="nav-icon" onClick={open}> Contact </div></li>
				</ul>

			 </div>
 		</nav>
 </Switch>
);

export default Toolbar;