import React from 'react';
import './Components.css';

const SideToolbar = ({close, SideToolbarOpen}) => {
	let Classes = "contact-nav-bar"


	if(SideToolbarOpen){
		Classes = "contact-nav-bar open "
	}
	return(
		<nav className = {Classes}>
				<div className = "">
					<div className="flex flex-wrap w-100 ml5">
						<div className = "w-90 tc"><p className = "f4">Contact me:</p></div>
						<div className = "w-10" onClick={close}><i className="material-icons bigX">close</i></div>
						<div className = "w-90 justify-center flex flex-wrap tc nav-items">
							{/* eslint-disable-next-line*/}
							<div className = "w-60 pa3"><a>Jack1gates@live.co.uk <i className="material-icons">email</i> &emsp;&emsp;</a>
							<a href="https://www.linkedin.com/in/jack-gates-b42598186/">LinkedIn <i className="material-icons">account_box</i> &emsp;&emsp;</a>
							<a href="https://github.com/jackgates73">Github <i className="material-icons">adb</i> &emsp;&emsp; </a></div>
						</div>
						<div className = "w-10"></div>
					</div>
				</div>
		</nav>
	);
};

export default SideToolbar;