	description: 'Buffer Overflow vulnerabilities are one of the most common ways in which a program can be exploited. As part of my university course we were tasked with developing a buffer overflow exploit, and write a paper on it…\n\nThe paper below will explain what a buffer overflow vulnerability is and how it can be exploited. For this investigation a vulnerable program was tested on. Using a debugger, the application was analysed in order to exploit the buffer overflow vulnerability. The paper also demonstrates the different techniques required in order to successfully exploit a buffer overflow attack in the form of tutorials that a user can follow. This includes techniques to get around obstacles such as Data execution prevention (DEP). An investigation into techniques to evade Intrusion Detection Systems was done and presented in the discussions section. Although the procedures show that the program was exploited with data execution prevention enabled and with it disabled using different methods, there were limitations to the exploits. Limited space in the stack meant that some exploit methods, such as the use of Return-Oriented Programming, were not able to be tested in full. However, the techniques to perform all of these methods were still demonstrated.',
