import React, {useState, useEffect} from 'react';
import './Components.css';
import pdfBinary from './projectFiles/hacking/binary hacking/exploit development final.pdf';
import pdfPenTesting from './projectFiles/hacking/web hacking/penetration test.pdf';
import pdfPenTestingReport from './projectFiles/hacking/web hacking/vulnerabilities found and mitigations.pdf';
import pdfNetworkHacking from './projectFiles/hacking/networking/finalnetwork2.pdf';
import pdfSwitchHacking from './projectFiles/hacking/networking/switch attacks.pdf';
import pdfStaticAnalysis from './projectFiles/development/static analysis/staticAnalysis.pdf';
import pdfAndroidMalware from './projectFiles/other/android malware/dissertation1.pdf';
import pdfAndroidMalwarePres from './projectFiles/other/android malware/poster final.pdf';
import pdfNetworkForensics from './projectFiles/other/forensics/digitalforensics2.pdf';
import pdfAuthenticationMechanism from './projectFiles/other/designing authentication/CMP417-UNIT2-GATES.pdf';
import pdfMicrosoftMalware from './projectFiles/other/microsoft malware/microsoftMalware.pdf';
import pdfWordpress from './projectFiles/other/wordpress plugins/WordPress presentation.pdf';
import zip2Dplatformer from './projectFiles/development/2dplatformer.zip';

const ProjectWindow = ({windowState, ProjectsDetail, selectedID}) => {
	let currentWindowState = "windowDesignProjectOpen"
	let currentID = parseInt(selectedID)

	const [projectFile, setProjectFile] = useState(ProjectsDetail[currentID].file)

	useEffect(() => {
		if (currentID === 0 || currentID === 5){
	    	setProjectFile(ProjectsDetail[currentID].file)
		}
		else {
			switch(currentID){
			case 1:
				setProjectFile(pdfAndroidMalware)
				return null;
			case 2:
				setProjectFile(pdfAuthenticationMechanism)
				return null;
			case 3:
				setProjectFile(pdfBinary)
				return null;
			case 4:
				setProjectFile(pdfNetworkForensics)
				return null;
			case 6:
				setProjectFile(pdfMicrosoftMalware)
				return null;
			case 7:
				setProjectFile(pdfSwitchHacking)
				return null;
			case 8:
				setProjectFile(pdfPenTesting)
				return null;
			case 9:
				setProjectFile(zip2Dplatformer)
				return null;
			case 12:
				setProjectFile(pdfStaticAnalysis)
				return null;
			case 13:
				setProjectFile(pdfWordpress)
				return null;
			case 14:
				setProjectFile(pdfNetworkHacking)
				return null;
			default:
				setProjectFile(ProjectsDetail[currentID].file)
				return null;
		}
		}
	// eslint-disable-next-line
	}, [currentID]);



	if(windowState){
		currentWindowState = "windowDesignProjectClosed"
	}
	return(
		<div  className = {currentWindowState}>
			<div className = "pa4 pb4 ph4">
				<h1 className="f2">{ProjectsDetail[currentID].title}</h1>
				<div className="f3 tl projDescription">{ProjectsDetail[currentID].description}</div>
				<div className="pa4">
					
					<div className="nav-items">
						{currentID !== 9 //this conditional operator ensures that if the 2d game is requested, it is downloaded
							?  currentID !== 8 //this conditional operator adds an additional link for the pen test report
								? currentID === 1 //this conditional operator adds an additional link for the dissertation demo
									? (
										<div>
											<a  href={projectFile}>Click to see more...</a> <br/> <br/> 
											<a href={pdfAndroidMalwarePres}>Demonstration Poster</a>
										</div> 
									)
									: <a href={projectFile}>Click to see more...</a>
								: (
									<div>
										<a  href={projectFile}>Click to see more...</a> <br/> <br/> 
										<a href={pdfPenTestingReport}>Penetration Test Report</a>
									</div> 
								)
							: <a href={projectFile} download>Download</a> 
			          	}
					</div>
				</div>
			</div>
		</div>
	);
};

export default ProjectWindow;