import React from 'react';
import './Components.css';

import androidApp from '../images/androidApp.png'
import androidMalware from '../images/androidMalware.png'
import biometrics from '../images/biometrics.png'
import bufferOverflow from '../images/bufferOverflow.png'
import forensics from '../images/forensics.png'
import homeYoga from '../images/homeYoga.png'
import macro from '../images/macro.png'
import mitm from '../images/mitm.png'
import pentesting from '../images/pentesting.jpg'
import platformer from '../images/platformer.png'
import poses from '../images/poses.png'
import poses2 from '../images/poses2.png'
import sast from '../images/sast.png'
import wordpress from '../images/wordpress.png'
import networkPT from '../images/networkPT.png'


const ProjectSelection = ({id, title, description, image, file, setSelectedID, selectedID}) => {

let picVariable = forensics
let selectionProperties = "w-90 center h-50 v-top projectSelectionButton"

if(selectedID === id){
	selectionProperties = "w-90 center h-50 v-top projectSelectionButtonSelected"
}


function renderPicture(id) {
	switch(id){
		case 0:
		 	picVariable = androidApp
			return null;
		case 1:
			picVariable = androidMalware
			return null;
		case 2:
			picVariable = biometrics
			return null;
		case 3:
			picVariable = bufferOverflow
			return null;
		case 4:
			picVariable = forensics
			return null;
		case 5:
			picVariable = homeYoga
			return null;
		case 6:
			picVariable = macro
			return null;
		case 7:
			picVariable = mitm
			return null;
		case 8:
			picVariable = pentesting
			return null;
		case 9:
			picVariable = platformer
			return null;
		case 10:
			picVariable = poses
			return null;
		case 11:
			picVariable = poses2
			return null;
		case 12:
			picVariable = sast
			return null;
		case 13:
			picVariable = wordpress
			return null;
		case 14:
			picVariable = networkPT
			return null;
		default:
			picVariable = androidApp
			return null;
	}
}
	return(
		<div  className = {selectionProperties} onClick={() => setSelectedID(id)} >
			<div className = "h-75 v-top">
				{renderPicture(id)}		
				<img className="h-100 w-100 v-top" alt="Logo" src={picVariable} />
			</div>
			<div className = "w-90 center tc pv2 f3"><p>{title}</p></div>
		</div>
	);
};

export default ProjectSelection;

